.footer {
  bottom: 0;
  width: 100%;
  min-height: 62px;
  display: flex;
  padding: 20px 32px;
  
  .logo {
    height: 23px;
    width: 151px;
  }

  &.alignment-right {justify-content: flex-end;}
  &.alignment-center { justify-content: center;}

  &.vertical-center { align-items: center; }
  &.vertical-bottom { align-self: flex-end; }

  &.color-dark {
    background-color: var(--color-black);
    .logo {
      width: 148px;
    }
  }

  &.size-large {
    padding: 40px 32px;
  }
}