body {
  /* theme colors */
  --color-primary: rgb(255, 153, 51);
  --color-primary-opacity-40: rgba(255, 153, 51, 0.40);
  --color-primary-darker: rgb(255, 128, 0);

  --color-white: rgb(255, 255, 255);
  --color-black: rgb(34, 34, 34);
  --color-window-bg: rgb(248, 248, 248);

  /* Background Colors */
  --color-loader-placeholder-bg: rgba(110, 106, 101, .4); 
  --color-shadow: rgba(158, 37, 38, 0.16);
  --color-container-shadow: rgba(0, 0, 0, 0.16);
  --color-error-shadow: rgba(158, 37, 38, 0.16);
  --color-error-bg: rgb(253, 240, 241);
  --color-info-shadow: rgba(158, 37, 38, 0.16);
  --color-info-bg: rgb(235, 245, 255);

  /* Border Colors */
  --color-border: rgb(229, 229, 229);
  --color-border-error: rgb(239, 121, 119);
  --color-border-info: rgb(151, 189, 250);
  
  /* Text Colors */
  --color-secondary-text: rgb(110, 106, 101); /* Below title, table headers, placeholders etc */
  --color-error: rgb(158, 37, 38);
  --color-info: rgb(31, 90, 219);

  /* Recipe Pill Colors */ 
  --color-emailpassword-bg: rgb(221, 252, 247);
  --color-emailpassword-text: rgb(0, 106, 91);
  --color-passwordless-bg: rgb(255, 234, 247);
  --color-passwordless-text: rgb(168, 17, 90);

  /* Social Provider Pill Colors */
  --color-google-bg: rgb(241, 222, 255);
  --color-google-text: rgb(92, 63, 121);
  --color-github-bg: rgb(222, 255, 238);
  --color-github-text: rgb(9, 108, 56);
  --color-facebook-bg: rgb(227, 235, 255);
  --color-facebook-text: rgb(21, 75, 221);
  --color-apple-bg: rgb(229, 237, 255);
  --color-apple-text: rgb(11, 28, 69);
  --color-custom-provider-bg: rgb(228, 224, 255);
  --color-custom-provider-text: rgb(84, 37, 176);
}
